<template>
    <v-alert @input="onClose" type="error" dismissible outline :value="true">
        {{ text }}
    </v-alert>
</template>

<script>
export default {
    props: ["text"],
    methods: {
        onClose() {
            this.$emit("dismissed");
        }
    }
};
</script>


